import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../BDTLL_common";
import { IStorage } from "../BDTLL";
import { StatusTooltip } from "../pages/components/statusTooltip";
import { AnalyzerPageStatus } from "../pages/analyzerPopup/analyzerPageStatus";

const ParseCriteria: {[key in BDTLL.SearchEngine]: Array<string>} = {
    [BDTLL.SearchEngine.SEARCH_GOOGLE]: [
        "a.l",
        ".s > .r > a",
        ".g > * > .r > a",
        ".g > .r > a",
        ".rc > .r > a",
        ".rc > div > a",
        ".pslimain > .r > a",
        ".g > a",
        ".gs_rt > a",
        "p > a.on",
        ".vsc .vsta a",
        "li.ads-ad h3 > a:last-child",
        "li > h3 > a[id~=vads]",
        ".DOqJne > g-link > a",
        '#tads      a:not([class*="aob"]):not(div.bOeY0b a):not(div.Qezod a):not(div.Vn4Xqe a):not(div.ifk4y a)',
        '#bottomads a:not([class*="aob"]):not(div.bOeY0b a):not(div.Qezod a):not(div.Vn4Xqe a):not(div.ifk4y a)',
        '#search div.yuRUbf > a', // Removed 'div.tF2Cxc' because it was too strong.
        '#search div.M42dy g-link > a',
        '#search div.ct3b9e > a',
    ],
    [BDTLL.SearchEngine.SEARCH_YAHOO]: [
        "div#cols .compTitle h1 a",
        "div#cols .compTitle h2 a",
        "div#cols .compTitle h3 a",
        "div#cols .compTitle h4 a",
        "div#cols .compTitle h5 a",
        "div#cols .compTitle h6 a",
        "div#cols .compList a",
        "div#cols .compTitle a",
        "div#cols .compText.lh-s a",
        // It like this is used only for jp "#contents__wrap .sw-CardBase .sw-Card__title a",
    ],
    [BDTLL.SearchEngine.SEARCH_YAHOO_JP]: [
        "#contents__wrap .sw-CardBase .sw-Card__title a",
    ],
    [BDTLL.SearchEngine.SEARCH_BING]: [
        "div.sb_tlst a",
        "div.sn_hd a",
        "div.sb_add h1 a, div.sb_add h2 a, div.sb_add h3 a, div.sb_add h4 a, div.sb_add h5 a, div.sb_add h6 a",
        "div.sa_uc a",
        "div.scs_child_rpr table a",
        "div#ans_news a",
        "li.b_algo h2 a",
        "li.b_algo h3 a",
        "li.b_top h2 a",
        "li.b_top h3 a"
    ],
    [BDTLL.SearchEngine.SEARCH_DUCKDUCKGO]: [
        "a.result__a:not(.related-searches a)",
        ".sponsored__sitelink"
    ]
}

interface ISearchResultLink {
    element?: HTMLElement,
    url?: string,
    request?: any,
    response?: BDTLL.PageStatus
}

class SearchAnalyzer {
    links: ISearchResultLink[];
    userWhitelist: Array<string>;
    advancedThreatFilterStatus: boolean;
    processAnchorFunctions: Map<string, Function>;

    constructor() {
        this.analyzeSearchResults();

        this.processAnchorFunctions = new Map<string, Function>();
        this.processAnchorFunctions.set(BDTLL.SearchEngine.SEARCH_GOOGLE, this.processGoogleAnchor);
        this.processAnchorFunctions.set(BDTLL.SearchEngine.SEARCH_YAHOO, this.processYahooAnchor);
        this.processAnchorFunctions.set(BDTLL.SearchEngine.SEARCH_YAHOO_JP, this.processYahooJpCoAnchor);
        this.processAnchorFunctions.set(BDTLL.SearchEngine.SEARCH_BING, this.processBingAnchor);
        this.processAnchorFunctions.set(BDTLL.SearchEngine.SEARCH_DUCKDUCKGO, this.processDuckDuckGoAnchor);

        const observer = new MutationObserver((mutations) => {
            const currentDomain = BDTLL.Utils.get_domain_for_link_scan(
                document.location.href
            );

            if (!(currentDomain in ParseCriteria)) {
                return;
            }

            for (const mutation of mutations) {
                if (mutation.type !== "childList") {
                    continue;
                }

                if (mutation.target.nodeName === "STYLE") {
                    continue;
                }

                mutation.addedNodes.forEach(async (addedNode) => {
                    let newLinks = this.extractLinks(addedNode, currentDomain);
                    if (newLinks.length > 0) {
                        newLinks = await this.scanLinks(newLinks);
                        this.links.push(...newLinks);
                        this.renderStatuses(currentDomain, this.links);
                    }
                });
            }
        });
        observer.observe(document, {
            subtree: true,
            childList: true,
        });
    }

    async analyzeSearchResults() {
        const currentDomain = BDTLL.Utils.get_domain_for_link_scan(
            document.location.href
        );

        if (!(currentDomain in ParseCriteria))
            return;

        await this.getWhitelist();
        await this.getAdvancedThreatFilterStatus();

        this.links = this.extractLinks(document, currentDomain);
        if (this.links.length === 0) {
            return;
        }

        this.links = await this.scanLinks(this.links);
        this.renderStatuses(currentDomain, this.links);
    }

    async getWhitelist() {
        this.userWhitelist = await whitelist;
    }

    async getAdvancedThreatFilterStatus() {
        this.advancedThreatFilterStatus = await tf;
    }

    extractLinks(rootNode: Node, domain: BDTLL.SearchEngine): ISearchResultLink[] {
        if (rootNode.nodeName === "#text") {
            return [];
        }

        try {
            const criteria = ParseCriteria[domain].join();
            let links = Array.from(
                (<Document>rootNode).querySelectorAll(criteria)
            ).map(
                this.getAnchorLink
            );
            links = links.filter(Boolean);

            return links;
        } catch (err) {
            if (BDTLL.DEBUG_MODE) {
                console.error(err);
            }

            return [];
        }
    }

    async scanLinks(links: ISearchResultLink[]): Promise<ISearchResultLink[]> {
        var requests = links.map(l => { return l.url });

        var response: any[] = await BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.SCAN_LINKS,
            urls: requests
        });

        if (BDTLL.DEBUG_MODE) {
            console.log("Sent message with requests");
        }
        for (let id in requests) {
            links[id].request = requests[id];
            let domain :string = BDTLL.Utils.extractRootDomain(links[id].url);

            // Check if the link should be scanned or not. If yes, first check if the url is in the user whitelist or not.
            if (!this.advancedThreatFilterStatus) {
                links[id].response = BDTLL.PageStatus.DISABLED;
            } else if (this.userWhitelist.indexOf(domain) > -1) {
                links[id].response = BDTLL.PageStatus.WHITELISTED;
            } else {
                links[id].response = response[id] as BDTLL.PageStatus;
            }
        }

        if (BDTLL.DEBUG_MODE) {
            console.log("Got repsonses");
            console.log(links);
        }

        return links;
    }

    getAnchorLink(a: any) {
        // Check for duplicates
        if (
            a.parentNode !== null &&
            a.parentNode.id.startsWith("BDTLL_")
        ) {
            return undefined;
        }

        if (
            a.previousElementSibling !== null &&
            a.previousElementSibling.id.startsWith("BDTLL_")
        ) {
            return undefined;
        }

        const divChildNodes = Array.from(
            a.querySelectorAll("div[id]") as NodeListOf<HTMLElement>
        );
        if (divChildNodes !== null) {
            for (const divNode of divChildNodes) {
                if (divNode.id.startsWith("BDTLL_")) {
                    return undefined;
                }
            }
        }

        let url = a.href;

        if (a.hasAttribute("data-preconnect-urls"))
            url = a.dataset.preconnectUrls;

        if (a.hasAttribute("data-href"))
            url = a.dataset.href;

        return {
            element: a.hasAttribute("data-pcu") ? a.querySelector("div") : a,
            url: url
        }
    }

    renderStatuses(currentDomain: BDTLL.SearchEngine, links: ISearchResultLink[]) {
        for (const [idx, link] of links.entries()) {
            if (document.getElementById(`BDTLL_${idx}`) !== null) {
                continue;
            }

            try {
                let anchor = this.processAnchorFunctions.get(currentDomain)(link);
                this.renderStatus(idx, link, anchor);
            } catch (err) { }
        }
    }

    processGoogleAnchor(link: ISearchResultLink) {
        let element = link.element.querySelector("h3") as HTMLElement;

        if (element !== null) {
            let span = document.createElement("span") as HTMLSpanElement;
            element.insertBefore(span, element.firstChild);
            element = span;
        }

        element = element || link.element.querySelector("span:not(cite > span):not(span > span)");
        element = element || link.element;

        return element;
    }

    processYahooAnchor(link: ISearchResultLink) {
        let element = link.element.querySelector("h3") as HTMLElement;

        element = element || link.element;

        return element;
    }

    processYahooJpCoAnchor(link: ISearchResultLink) {
        let element = link.element.querySelector("h3") as HTMLElement;

        if (element !== null) {
            let span = element.querySelector("span") as HTMLSpanElement;
            element = span || element;
        }

        element = element || link.element;

        return element;
    }

    processBingAnchor(link: ISearchResultLink) {
        return link.element;
    }

    processDuckDuckGoAnchor(link: ISearchResultLink) {
        return link.element;
    }

    renderStatus(id: number, link: ISearchResultLink, anchor: HTMLElement) {
        const statusTooltipNode = anchor.parentNode.insertBefore(
            this.renderStatusTooltip(id, link),
            anchor
        );

        this.renderStatusPopup(id, link);

        statusTooltipNode.addEventListener('mouseout', () => {
            let popup = document.getElementById(`TLL_${id}`);
            if (popup !== undefined) {
                popup.style.display = "none";
                (popup.parentNode as HTMLElement).style.display = "none";
            }
        });

        statusTooltipNode.addEventListener('mouseover', () => {
            let rect = statusTooltipNode.getBoundingClientRect();
            let popup = document.getElementById(`TLL_${id}`);
            if (popup !== undefined) {
                popup.style.display = "block";
                popup.style.left = (rect.left + window.scrollX).toFixed(0) + "px";
                popup.style.top = (rect.top + window.scrollY + 22).toFixed(0) + "px";
                (popup.parentNode as HTMLElement).style.display = "block";
            }
        });
    }

    renderStatusPopup(id: number, link: ISearchResultLink) {
        let container = document.createElement('div');
        container.style.display = "none";

        document.body.appendChild(container);

        this.renderNode(
            AnalyzerPageStatus,
            {
                id: `TLL_${id}`,
                url: link.url,
                threatStatus: link.response,
                linkScannerActive: true,
            },
            container,
        )
    }

    renderStatusTooltip(id: number, link: ISearchResultLink) {
        let container = document.createElement('div');

        container.setAttribute("id", `BDTLL_${id}`);
        container.style.display = "inline";
        container.style.float = "left";

        container.addEventListener('click', (event) => {
            // When clicking on the status bullet open new tab with bitdefender page.
            window.open(`${BDTLL.Consts.SEARCH_INFO_URL}${link.url}`, "_blank");

            event.preventDefault();
            event.stopPropagation();
            event.stopImmediatePropagation();

            return false;
        });
        
        this.renderNode(
            StatusTooltip,
            {
                url: BDTLL.Consts.SEARCH_INFO_URL + link.url,
                status: link.response
            },
            container,
        );

        return container;
    }

    renderNode(component: any, attributes: any, container: HTMLDivElement) {
        ReactDOM.render(
            React.createElement(component, attributes, null),
            container
        );
    }
}

let tf = BDTLL.MessageService.messageBackground({
        command: BDTLL.Command.GET_SETTING,
        type: BDTLL.SettingType.THREAT_FILTER
    }
);

let whitelist = BDTLL.MessageService.messageBackground({
        command: BDTLL.Command.GET_WHITELIST
    }
);

let SA = new SearchAnalyzer();
