import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import "./tooltip.css";

interface IStatusTooltipProps {
    url: string;
    status: BDTLL.PageStatus;
}

export class StatusTooltip extends React.Component<IStatusTooltipProps> {
    imgSrc: string;

    constructor(props: IStatusTooltipProps) {
        super(props);

        const icon = BDTLL.toBarIcon[props.status];
        this.imgSrc = BDTLL.Utils.getExtensionUrl(icon);
    }

    render() {
        return (
            <img className="BDTLL_status" src={this.imgSrc}></img>
        );
    }
}
